import React from "react";
import styled from "styled-components";

const ParralaxContainer = styled.div`
  min-height: 100vh;
  padding: 30vw 0 5vw;
  position: relative;
  transform-style: inherit;
  width: 100vw;
  background: 50% 50% / cover;
  &:before {
    content: "Back Layer";
    display: block;
    background: 50% 50% / cover;
    bottom: 0;
    top: 0;
    right: 0;
    left: 0;
    position: absolute;
    background-color: dodgerblue;
    color: white;
  }
`;

const ParallaxBaseLayer = styled.div`
  margin-top: -100px;
`;

const Parallax = ({ children }) => (
  <ParralaxContainer>
    <ParallaxBaseLayer>{children}</ParallaxBaseLayer>
  </ParralaxContainer>
);
export default Parallax;



